@use 'vars' as *;

.register-page{
    padding: 82px 0;
    background: #F6F8FC;
    min-height: 100vh;
    @include mq{
        padding: 50px 0;
    }
    &__wp{
        max-width: 690px;
        width: 100%;
        margin: 0 auto;
        background: #fff;
        padding: 40px 30px;
        border-radius: 16px;
        @include mq{
            padding: 24px 20px;
        }
    }
    &__ttl{
        text-align: center;
        @include font-size(40);
        font-weight: 600;
        margin-bottom: 24px;
        @include mq{
            @include font-size(26);
        }
    }
}

//styles page detail
.engineer-detail{
    padding: 16px 0 216px;
    background: #F6F8FC;
    @include mq{
        padding-bottom: 50px;
    }
    &__heading{
        @include flex(between, center);
        margin-bottom: 35px;
        @include mq{
            margin-bottom: 26px;
        }
        .c-ttl__02{
            margin-bottom: 0;
        }
        .c-btn__pdf{
            @include mq{
                display: none;
            }
        }
    }
    &__intro{
        background: #fff;
        border-radius: 16px;
        box-shadow: 2px 2px 10px 0px #7E91D324;
        @include flex(0, start, wrap);
        padding: 10px 13px;
        margin-bottom: 16px;
        @include mq(sm){
            padding: 0;
        }
        &-img{
            width: 31.6%;
            border-radius: 16px;
            overflow: hidden;
            @include mq(tb){
                width: 45%;
            }
            @include mq{
                width: 50%;
            }
            @include mq(sm){
                width: 100%;
                border-radius:  16px 16px 0 0;
            }
        }
        &-content{
            width: calc(100% - 31.6%);
            padding-left: 30px;
            padding-top: 6px;
            @include mq(tb){
                width: 55%;
            }
            @include mq{
                width: 50%;
                padding-left: 15px;
            }
            @include mq(sm){
                width: 100%;
                padding-left: 0;
                padding-top: 15px;
            }
        }
        &-name{
            @include fts(32, 44.8);
            font-weight: 600;
            margin-bottom: 12px;
            @include mq{
                @include font-size(24);
                text-align: center;

            }
        }
        &-info{
            color: #94A3B8;
            margin-bottom: 10px;
            @include flex(0, 0, wrap);
            gap: 15px 0;
            @include mq{
                max-width: 250px;
                justify-content: center;
                margin: 0 auto;
                position: relative;
                left: -10px;
            }
            .birthday{
                @include font-size(16);
                margin-right: 30px;
                position: relative;
                padding-left: 32px;
                @include mq{
                    margin-right: 0;
                    margin-bottom: 18px;
                }
                @include p-before(24px, 24px){
                    background: url(../img/common/cake.png) no-repeat center/100%;
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%);
                }
            }
            .address{
                @include font-size(14);
                padding-left: 32px;
                position: relative;
                @include p-before(24px, 24px){
                    background: url(../img/common/location.png) no-repeat center/100%;
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%);
                }
            }
        }
        &-desc{
            @include fts(14, 19.6);
            margin-bottom: 10px;
            padding-right: 22px;
            @include mq{
                margin-bottom: 0;
                padding: 16px 16px 32px;
                margin-top: 20px;
                border-top: 1px solid #E2E8F0;
            }
            p{
                margin-bottom: 25px;
                @include mq{
                    margin-bottom: 22px;
                }
                &:last-of-type{
                    margin-bottom: 0;
                }
            }
        }
        &-control{
            @include flex(0, 0, wrap);
            @include mq{
                position: fixed;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                padding: 8px 16px;
                background: #fff;
                z-index: 10;
                justify-content: center;
            }
            .c-btn__submit{
                max-width: 228px;
                margin-top: 0;
                @include mq{
                    max-width: 269px;
                    height: 48px;
                }
            }
        }
        &-favorite{
            width: 48px;
            margin-right: 24px;
        }
    }
    // &__private{
    //     overflow: hidden;
    //     border-radius: 16px;
    //     & > *{
    //         display: none;
    //     }
    //     > img{
    //         display: block;
    //     }
    // }
    &__main{
        background: #fff;
        padding: 24px 34px;
        margin-bottom: 65px;
        border-radius: 16px;
        @include mq{
            padding: 20px 16px;
            border-radius: 10px;
            margin-bottom: 30px;
        }
        &-box{
            margin-bottom: 27px;
            @include fts(16, 22.4);
            &:last-of-type{
                margin-bottom: 0;
            }
            @include mq{
                @include fts(14, 19.6);
            }
            
        }
        &-table{
            border-radius: 12px;
            overflow: hidden;
            @include mq{
                border-radius: 12px 0 0 12px;
                overflow-x: scroll;
                margin-right: -16px;
                &::-webkit-scrollbar {
                    width: 12px;
                }
                 
                &::-webkit-scrollbar-track {
                    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
                    border-radius: 10px;
                }
                 
                &::-webkit-scrollbar-thumb {
                    border-radius: 10px;
                    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
                }
            }
            table{
                width: 1222px;
                background: #fff;
                tr{
                    th{
                        font-weight: 600;
                        color: #777E90;
                        background: #E2E8F0;
                    }
                    th, td{
                        @include fts(16, 22.4);
                        vertical-align: top;
                        padding: 19px 16px;
                        @include mq{
                            @include font-size(14);
                            padding: 15px 16px;
                        }
                        &:nth-child(1), &:nth-child(3){
                            width: 200px;
                        }
                        &:nth-child(2){
                            width: 300px;
                        }
                        &:nth-child(4){
                            width: 530px;
                        }
                    }
                    td{
                        padding: 13px 16px;
                        border-left: 1px solid #E2E8F0;
                        border-bottom: 1px solid #E2E8F0;
                        &:last-of-type{
                            border-right: 1px solid #E2E8F0; 
                        }
                        .c-dots{
                            line-height: 1.34;
                            li{
                                margin-bottom: 0;
                            }
                        }
                    }
                }
            }
            &--styles02{
                table{
                    tr{
                        th, td{
                            &:nth-child(1){
                                width: 16.39%;
                            }
                            &:nth-child(2){
                                width: 49.18%;
                            }
                            &:nth-child(3){
                                width: 35.24%;
                            }
                        }
                    }
                }
            }
        }
        &-pr{
            strong{
                display: block;
                margin-bottom: 6px;
            }
            @include mq{
                @include fts(16, 22.4);
            }
            p{
                margin-bottom: 10px;
                @include mq{
                    margin-bottom: 16px;
                }
                &:last-of-type{
                    margin-bottom: 0;
                }
            }
        }
    }
    &__control{
        max-width: 190px;
        margin: 0 auto;
    }
}

.c-dots{
    @include fts(16, 22.4);
    @include mq{
        @include fts(14, 19.6);  
    }
    li{
        margin-bottom: 10px;
        @include mq{
            margin-bottom: 17px;
        }
        &:last-of-type{
            margin-bottom: 0;
        }
        ul{
            padding-left: 47px;
        }
    }
    &--styles02{
        @include mq{
            @include fts(16, 22.4);
            li{
                margin-bottom: 0;
            }
        }
    }
}

.jobs-list{
    @include flex(0, 0, wrap);
    @include fts(16, 22.4);
    li{
        width: 42%;
        margin-bottom: 16px;
        @include mq{
            width: 100%;
        }
        span{
            font-weight: 600;
            @include mq{
                margin-right: 15px;
            }
        }
    }
}